@import "https://fonts.googleapis.com/css?family=Roboto+Mono";
.countdown {
  -webkit-font-smoothing: antialiased;
  width: 100%;
  height: 100%;
  text-align: center;
  flex-direction: column;
  font-family: Roboto Mono, sans-serif;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.countdown__timer {
  perspective: 500px;
  height: 10rem;
  margin: 0 auto;
  display: flex;
}

.countdown.remove {
  animation: hide-countdown 1s cubic-bezier(0, .9, .56, 1.2) forwards;
  overflow: hidden;
}

.countdown.section {
  position: relative;
}

.countdown__value, .countdown__separator {
  color: #333;
  height: 8rem;
  text-align: center;
  width: 100%;
  font-size: 5rem;
  line-height: 10rem;
  display: block;
  position: relative;
}

.countdown__separator {
  width: 2rem;
  margin: 0;
}

.countdown__actions {
  z-index: 1;
  justify-content: center;
  gap: 10px;
  display: flex;
  position: relative;
}

.countdown__datepicker {
  text-align: center;
  font-size: 2rem;
}

.countdown__toggle {
  cursor: pointer;
  width: 7rem;
  font-size: 1.5rem;
}

.countdown__days:after, .countdown__hours:after, .countdown__minutes:after, .countdown__seconds:after {
  content: "DAYS";
  text-align: center;
  font-size: 16px;
  position: absolute;
  bottom: -80px;
  left: 0;
  right: 0;
}

.countdown__hours:after {
  content: "HOURS";
}

.countdown__minutes:after {
  content: "MINUTES";
}

.countdown__seconds:after {
  content: "SECONDS";
}

.new, .old, .current {
  color: #333;
  height: 10rem;
  width: 100%;
  border-radius: 1rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.new {
  animation: show-new .4s cubic-bezier(0, .9, .5, 1.2) forwards;
}

.old {
  animation: hide-old 2s cubic-bezier(0, .9, .56, 1.2) forwards;
}

@keyframes hide-countdown {
  to {
    height: 0;
    overflow: hidden;
  }
}

@keyframes show-new {
  0% {
    opacity: 0;
    transform: translate(-50%, -50%)translateY(-2rem)scale(.8)rotateX(-20deg);
  }

  100% {
    transform: translate(-50%, -50%);
  }
}

@keyframes hide-old {
  0% {
    transform: translate(-50%, -50%);
  }

  100% {
    opacity: 0;
    transform: translate(-50%, -50%)translateY(-5rem)scale(.5)rotateX(-75deg);
  }
}

